<template>
  <BContainer>
    <BRow>
      <BCol sm="6" class="mx-auto">
        <BFormGroup>
          <BFormInput
            id="url"
            placeholder="Enter your URL"
            @input="changeOption($event, 'url')"
          />
        </BFormGroup>
      </BCol>
    </BRow>
    <BRow>
      <BCol sm="6" class="mx-auto text-center">
        <BFormGroup>
          <BFormSelect
            @change="changeOption($event, 'size')"
            v-model="options.size"
          >
            <template v-for="num in 60">
              <BFormSelectOption v-if="num >= 2" :key="num" :value="num">
                {{ num }}
              </BFormSelectOption>
            </template>
          </BFormSelect>
        </BFormGroup>
        <BFormGroup>
          <BFormCheckbox
            @input="changeOption($event, 'isTransparent')"
            switch
            size="lg"
            >Transparent background</BFormCheckbox
          >
        </BFormGroup>
        <BFormGroup>
          <BFormRadioGroup
            :options="radioOptions"
            v-model="options.selectedExt"
            name="radio-inline"
            buttons
            @input="changeOption($event, 'selectedExt')"
          ></BFormRadioGroup>
        </BFormGroup>
      </BCol>
    </BRow>
  </BContainer>
</template>

<script>
export default {
  name: "Form",
  data: () => ({
    radioOptions: [
      { text: "PNG", value: "png" },
      { text: "SVG", value: "svg" },
    ],
  }),
  props: {
    options: {
      type: Object,
      required: () => {},
    },
  },
  methods: {
    changeOption(v, k) {
      this.$emit("onChangeOption", { key: k, value: v });
    },
  },
};
</script>

<style scoped lang="scss"></style>
