<template>
  <BRow>
    <BCol sm="6" class="mx-auto text-center">
      <div class="qr-code">
        <BImg v-if="isFormValid" :src="fullUrl" fluid :alt="options.url" />
        <span v-else class="no-qr-code">Your QR code here</span>
      </div>
    </BCol>
  </BRow>
</template>

<script>
export default {
  name: "QrCode",
  props: {
    options: {
      type: Object,
      required: true,
    },
    isFormValid: {
      type: Boolean,
      required: true,
    },
  },
  computed: {
    fullUrl() {
      return `https://qrtag.net/api/qr${
        this.options.isTransparent ? "_transparent" : ""
      }_${this.options.size}.${this.options.selectedExt}?url=${
        this.options.url
      }`;
    },
  },
};
</script>

<style scoped lang="scss">
.qr-code {
  background: #555;
  padding: 20px;
  border-radius: 5px;
}

.no-qr-code {
  position: relative;
  padding: 10px;
  font-size: 20px;
  text-transform: uppercase;
}
</style>
