<template>
  <div id="app">
    <BContainer class="pt-5">
      <h1 class="text-center mb-4">QR Code Generator</h1>
      <Form :options="options" @onChangeOption="updateOptions" />
      <QrCode :options="options" :isFormValid="isFormValid" />
    </BContainer>
  </div>
</template>

<script>
import Form from "./components/Form.vue";
import QrCode from "./components/QrCode.vue";

export default {
  name: "App",
  data: () => ({
    options: {
      url: "",
      size: 5,
      isTransparent: "",
      selectedExt: "png",
    },
  }),
  computed: {
    isFormValid() {
      //eslint-disable-next-line
      const regexp = /^(?:http(s)?:\/\/)?[\w.-]+(?:\.[\w\.-]+)+[\w\-\._~:/?#[\]@!\$&'\(\)\*\+,;=.]+$/gm;

      return Boolean(this.options.url.length && this.options.url.match(regexp));
    },
  },
  components: {
    Form,
    QrCode,
  },
  methods: {
    updateOptions(obj) {
      this.options[obj.key] = obj.value;
    },
  },
};
</script>

<style lang="scss">
html,
body,
#app {
  height: 100%;
}

#app {
  display: flex;
